<template lang="html">
    <div class="invoices">
      <div class="invoices-title clear">
          <h2>发票管理 / </h2> <p>吼吼吼吼吼吼吼吼吼吼吼吼</p>
          <el-button :plain="true" type="success">EXCEL导出</el-button>
      </div>
      <div class="invoices-content">
          <div class="cont-top clear">
            <div class="inv-wrap clear">
              <div class="div1">
                受票方
                <el-input v-model="input1" placeholder="请输入订单号"></el-input>
              </div>
              <div class="div1">
                开票状态
                <el-input v-model="input2" placeholder="请选择下单区间"></el-input>
              </div>
              <div class="div1">
                申请时间
                <el-input v-model="input3" placeholder="选择"></el-input>
              </div>

              <div class="div1">
                发票代码
                <el-input v-model="input4" placeholder="选择"></el-input>
              </div>
              <div class="div1">
                运单号
                <el-input v-model="input5" placeholder="选择"></el-input>
              </div>
              <div class="div1">
                开票时间
                <el-input v-model="input6" placeholder="选择"></el-input>
              </div>
              <el-button type="primary">搜索</el-button>
            </div>

          </div>
          <div class="cont-button">
              <div class="but-top">
                  <p class="p1">未通过：<span>0.00</span>元
                  &nbsp;&nbsp;申请中：<span>0.00</span>元
                  &nbsp;&nbsp;已开票：<span>575150.00</span>元</p>
                  <p class="p2 ">合计：<span>575150.00</span>元</p>
              </div>
              <div class="but-buttom">
                <el-table
                   ref="multipleTable"
                   :data="tableData3"
                   border
                   tooltip-effect="dark"
                   style="width: 100%">
                   <el-table-column
                   label="发票代码"
                   width="120">
                   <template scope="scope">{{ scope.row.msg}}</template>
                   </el-table-column>
                   <el-table-column
                   prop="name"
                   label="客户(受票方)"
                   width="230">
                   <!-- <template scope="scope">
                     <span>{{scope.row.name}}</span>
                     <p>{{scope.row.phone}}</p>
                   </template> -->
                   </el-table-column>
                   <el-table-column
                   prop="address"
                   label="开票额"
                   show-overflow-tooltip>
                   <template scope="scope">
                       <span>{{scope.row.address}}</span>
                      <!-- <p>{{scope.row.date}}</p> -->
                   </template>
                   </el-table-column>
                   <el-table-column
                   prop="zt"
                   label="开票状态"
                   show-overflow-tooltip>
                   <!-- <template scope="scope">
                       <span>{{scope.row.address}}</span>
                      <p>{{scope.row.date}}</p>
                   </template> -->
                   </el-table-column>
                   <el-table-column
                   prop="date"
                   label="申请时间"
                   show-overflow-tooltip>

                   </el-table-column>
                   <el-table-column
                   prop="date2"
                   label="开票时间"
                   show-overflow-tooltip>

                   </el-table-column>
                   <el-table-column
                   prop="sfyf"
                   label="实付运费"
                   show-overflow-tooltip>

                   </el-table-column>



                   <el-table-column
                   prop=""
                   label="开票信息"
                   show-overflow-tooltip>
                   <template scope="scope">
                      <router-link :to="{path:'/billcord'}" tag="a" style="color:#2657CE" >查看</router-link>
                       <!-- <el-button type="text" @click="vehicle">查看</el-button> -->


                   </template>
                   </el-table-column>
                   <el-table-column
                   prop="bz"
                   label="备注"
                   show-overflow-tooltip>

                   </el-table-column>
               </el-table>
              </div>
          </div>
      </div>
    </div>
</template>

<script>
export default {
  data() {
   return {
     input1: '',
     input2: '',
     input3: '',
     input4: '',
     input5: '',
     input6: '',
     tableData3: [{
       msg: '00453391',
       name: '安东石油技术(集团)有限公司',
      //  phone:'15064558893',
       address: '228900.00',
       date:'2016-11-17',
       date2:'2017-05-05',
       zt:'已开票',
       sfyf:'176990.00',
       bz:'哈哈哈哈哈哈哈哈哈'
     },{
       msg: '00453391',
       name: '安东石油技术(集团)有限公司',
      //  phone:'15064558893',
       address: '228900.00',
       date:'2016-11-17',
       date2:'2017-05-05',
       zt:'已开票',
       sfyf:'176990.00',
       bz:'哈哈哈哈哈哈哈哈哈'
     },{
       msg: '00453391',
       name: '安东石油技术(集团)有限公司',
      //  phone:'15064558893',
       address: '228900.00',
       date:'2016-11-17',
       date2:'2017-05-05',
      zt:'已开票',
      sfyf:'176990.00',
      bz:'哈哈哈哈哈哈哈哈哈'
     },
     {
       msg: '00453391',
       name: '安东石油技术(集团)有限公司',
      //  phone:'15064558893',
       address: '228900.00',
       date:'2016-11-17',
       date2:'2017-05-05',
       zt:'已开票',
       sfyf:'176990.00',
       bz:'哈哈哈哈哈哈哈哈哈'
     }],
     multipleSelection: []

   };
   }
 }

</script>

<style lang="less" scoped>
.invoices{
  width: 1200px;
  margin: 0 auto;
  border: 1px solid #E8E8F0;
  background: #FAFBFF;
  .invoices-title{
    width: 1140px;
    height: 90px;
    line-height: 90px;
    margin: 0 auto;
    color: #3f528a;
    h2{
      float: left;
    }
    p{
      float: left;
      font-size: 13px;
      margin-left: 20px;
    }
    button{
      float: right;
      margin-top: 28px;
    }
  }
  .invoices-content{
    width: 1140px;
    height: 1000px;
    border: 1px solid #e6e9f0;
    margin: 0  auto;
    background: #fff;
    .cont-top{
      border-bottom: 2px solid #e6e9f0;
      width: 1100px;

      margin: 0 auto;
      height: 230px;
      color: #3f528a;
      .inv-wrap{
        width: 930PX;

        text-align: center;
        margin-top: 15px;
        .div1{

          margin-top: 20px;
          width: 300px;
          height: 50px;
          // text-align: center;
          line-height: 50px;
          float: left;
          font-weight: 700;
          font-size: 14px;
          .el-input{
            float: right;
            width: 230px;
            input{
              background: #f5f6fb;
            }
          }

        }
        .el-button{
          width: 220px;
          margin-top: 30px;
        }
      }
    }
    .cont-button{
      width: 1100px;
      // border: 1px solid;
      margin: 0 auto;
      height: 230px;
      color: #3f528a;
      .but-top{
        height: 30px;
        border: 1px solid #E8E8F0;
        background: #fffcfd;
        margin-top: 12px;
        line-height: 30px;
        .p1{
          float: left;
          span{
            color: red;
          }
        }
        .p2{
          float: right;
          span{
            color: red;
          }
        }
      }
      .but-buttom{
        margin-top: 10px;
        .el-table{
          .el-table__header-wrapper{
            .cell{
              color: #909CB2;
            }
          }
          .el-table__body-wrapper{
            tr{
              height: 80px;
            }
          }
        }
      }
    }
  }
}
</style>



